.App {
  text-align: center;
    line-height: 3em;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.card{
    margin-top: 30px;
    text-align: center;
}

.delete{
    min-width: 100%;
    word-wrap: break-word;
    background-color: #fff;
    background-clip: border-box;
    border: 1px solid rgba(0,0,0,.125);
    border-radius: .25rem;
   padding: .99rem 1.25rem;
    
}
header{
   background: #1bb4dd;
  color: white;
  padding:3vmin 1rem;
  text-align: center;
  margin: 0 0 8vmin 0;
    display: flex!important;
    flex-direction: row;
    justify-content: space-around;
    align-items:stretch;
     
}
.App ul li{
    text-decoration: none;
    list-style-type: none;
    
        
}

.item{
    text-align: left;
    padding: 2%;
}

.item h1{
    text-align: center;
    margin-bottom:2em;
}
.item ul{
    margin-left:3%;
}
.item ul li{
list-style-type: circle;
}